import React from 'react'
import {BrowserRouter as Router,Route,Link,Switch} from 'react-router-dom'//

import { Tabs,ArticleDetail } from './components'

import { observable } from 'mobx';
import { Provider } from 'mobx-react';

import './App.less'

const One = () => (
    <div>
        <h2></h2>
    </div>
)

const Two = () => (
    <div>
        <h2></h2>
    </div>
)

const Lists = ({ match }) => (
    <div>
        <h3>{match.params.ListsId}</h3>
    </div>
)

const List = ({ match }) => (
    <div>
        <h2></h2>
        <ul>
            <li>
                <Link to={`${match.url}/`}>
                    
                </Link>
            </li>
            <li>
                <Link to={`${match.url}/`}>
                    
                </Link>
            </li>
            <li>
                <Link to={`${match.url}/`}>
                    
                </Link>
            </li>
        </ul>
        <Route path={`${match.url}/:ListsId`} component={Lists}/>
        <Route exact path={match.url} render={() => (
            <h3>url...</h3>
        )}/>
    </div>
)

const testObj = observable({
    a: '2',
    b: '3'
})

const RouterList = () => (
    <Router>
        <div>
            <Switch>
              <Route exact path="/" component={Tabs}/>
              <Route path="/post/:id" component={ArticleDetail}/>
              <Route path="/Lists" component={List}/>
            </Switch>
        </div>
    </Router>
)

const App = () => (
    <Provider testObj={testObj}>
        <RouterList />
    </Provider>
)

export default App



